.header {
	width: 100%;
	z-index: 11;

	&__fixed {
		top: -200px;
		transition: top 1s;
		height: 86px;
	}

	&__fixed-placeholder {
		height: 86px;
		display: none;

		@media (max-width: 1200px) {
			height: 80px;
		}
	}

	&__container {
		position: relative;
		z-index: 2;
		max-width: 1800px;
		margin: auto;
		padding: 0 15px;
		display: flex;
		align-items: center;

		@media (max-width: 1200px) {
			justify-content: space-between;

			&.active .menu__body {
				opacity: 1;
				visibility: visible;
			}

			&.active {
				.logo__img-1 {
					display: none;
				}

				.logo__img-2 {
					display: block;
				}
			}
		}
	}

	&__menu {
		@media (max-width: 1200px) {
			order: -1;
		}
	}

	&__btn {
		@media (min-width: 1201px) {
			margin-left: 31px;
		}

		@media (max-width: 1200px) {
			margin: 30px auto 0;
		}
	}

	&__tel {
		position: relative;
		z-index: 5;
		font-weight: 700;
		font-size: 24rem;
		color: #fff;
		opacity: 0.75;

		@media (min-width: 1201px) {
			margin-left: auto;
		}

		&:hover {
			opacity: 1;
			color: #fff;
		}

		span {
			@media (max-width: 1200px) {
				display: none;
			}
		}

		img {
			width: 35px;
			border: 1px solid $color-primary;
			border-radius: 50%;
			display: none;

			@media (max-width: 1200px) {
				display: block;
			}
		}
	}

	&-main {
		position: absolute;
		top: 50px;

		@media (max-width: 1200px) {
			top: 20px;
		}

		&--fixed {
			.header__fixed {
				background-color: #fff;
				box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				z-index: 10;
				padding: 15px 0;
			}

			.menu__icon span,
			.menu__icon::before,
			.menu__icon::after {
				background-color: $color-primary;
			}

			.header__fixed-placeholder {
				display: block;
			}

			.header__tel {
				opacity: 1;
			}

			.logo__img-1 {
				display: none;
			}

			.logo__img-2 {
				display: block;
				width: 63px;
				height: 56px;
			}

			.menu__link {
				color: #383838;

				&:hover {
					color: $color-primary;
				}
			}

			.header__tel {
				color: $color-primary;

				&:hover {
					color: #383838;
				}
			}

			.menu__list li:hover .menu__link {
				color: $color-primary;
			}
		}
	}

	&-inner {
		position: fixed;
		top: 0;
		padding: 20px 0;
		box-shadow: 0px 4px 94px rgba(0, 0, 0, 0.05);
		background-color: #fff;

		.menu__link {
			color: #383838;

			&:hover {
				color: $color-primary;
			}
		}

		.menu__icon span,
		.menu__icon::before,
		.menu__icon::after {
			background-color: $color-primary;
		}

		.header__tel {
			color: $color-primary;
			opacity: 1;

			&:hover {
				color: #383838;
			}
		}

		.menu__list li:hover .menu__link {
			color: $color-primary;
		}
	}
}

.menu {
	@media (min-width: 1201px) {
		margin-left: 50rem;
	}

	&__body {
		@media (max-width: 1200px) {
			background-color: #fff;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 130px 20px 20px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 106px;
				z-index: 50;
			}
		}
	}

	&__list {
		display: flex;

		@media (max-width: 1200px) {
			flex-direction: column;
		}

		li {
			position: relative;

			@media (max-width: 1200px) {
				border-bottom: 1px solid #ccc;

				&:first-child {
					border-top: 1px solid #ccc;
				}
			}

			&:hover {
				@media (min-width: 1201px) {
					.menu__sub-list {
						opacity: 1;
						visibility: visible;
						transform: translate(0, 10%);
						pointer-events: all;
					}

					.menu__link {
						opacity: 1;
					}
				}
			}
		}

		&>li+li {
			margin-left: 30px;

			@media (max-width: 1540px) {
				margin-left: 20px;
			}

			@media (max-width: 1200px) {
				margin-left: 0;
			}
		}
	}

	&__link {
		padding: 12rem 0;
		font-weight: 700;
		color: #fff;
		opacity: 0.75;

		&:hover {
			opacity: 1;
			color: #fff;
		}

		@media (max-width: 1200px) {
			display: block;
			padding: 20rem 0;
			color: #383838;
			opacity: 1;
		}
	}

	&__arrow {
		@media (max-width: 1200px) {
			&::before {
				content: '';
				background-image: url('../img/svg/arrow-down.svg');
				background-size: 30rem;
				background-position: center;
				background-repeat: no-repeat;
				height: 60rem;
				width: 80rem;
				z-index: 20;
				position: absolute;
				top: 0;
				right: 0;
				cursor: pointer;
				transition: all 0.3s ease;
			}

			&.active {
				&::before {
					transform: rotate(180deg);
				}
			}
		}
	}

	&__sub-list {
		@media (min-width: 1201px) {
			position: absolute;
			top: 100%;
			left: 0;
			transform: translate(0px, 10%);
			background-color: #fff;
			opacity: 0;
			visibility: hidden;
			box-shadow: 0px 4px 94px rgba(0, 0, 0, 0.05);
			padding: 20px;
			min-width: 250px;
			pointer-events: none;
			transition: all 0.3s ease 0s;
		}

		@media (max-width: 1200px) {
			display: none;
		}

		li+li {
			@media (min-width: 1201px) {
				margin-top: 10px;
			}
		}

		li {
			&:last-child {
				border-bottom: none;
			}
		}
	}

	&__sub-link {
		font-weight: 700;
		color: #383838;

		@media (min-width: 1201px) {
			opacity: 0.75;
		}

		@media (max-width: 1200px) {
			display: block;
			padding: 20px 0;
		}
	}

	&--btn {

		.menu__icon span,
		.menu__icon::before,
		.menu__icon::after {
			background-color: $color-primary;
		}
	}
}

@media (max-width: 1200px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35px;
		height: 20px;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: #fff;
		border-radius: 10px;
	}

	.menu__icon.active:before,
	.menu__icon.active:after {
		background-color: $color-primary;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0px, -50%);
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}
}