.btn {
	position: relative;
	display: inline-block;
	color: #fff;
	background-color: $color-primary;
	border-radius: 100px;
	border: 1px solid transparent;
	text-transform: uppercase;
	transition: all 0.3s ease-out;
	text-align: center;
	font-size: 12rem;
	font-weight: 700;
	padding: 15rem 34rem;
	transform: perspective(1px) translateZ(0);
	transition-property: color;
	transition-duration: 0.3s;

	@media (min-width: 992px){
		overflow: hidden;
	}

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		transform: scaleX(0);
		transform-origin: 0 50%;
		transition-property: transform;
		transition-duration: 0.3s;
		transition-timing-function: ease-out;
	}

	&:hover:before {
		transform: scaleX(1);
	}

	&:hover {
		color: $color-primary;
		border-color: $color-primary;
	}

	// @media (max-width: 575px) {
	// 	font-size: 14px;
	// 	padding: 20px;
	// 	text-align: center;
	// 	width: 100%;
	// }

	&--animate {
		height: 250px;
		width: 250px;
		position: absolute;
		top: -20px;
		left: -120px;
		background: rgba(255, 255, 255, 1.0);
		border-radius: 100%;
		transform: scale(0);
		animation: ripple 4s linear infinite;
	}

	@keyframes ripple {
		20% {
			opacity: 0;
			transform: scale(2.5);
		}

		100% {
			opacity: 0;
			transform: scale(2.5);
		}
	}

	// &:disabled {
	// 	opacity: 0.5;
	// 	cursor: default;
	// 	color: #fff;

	// 	&:before,
	// 	&:hover:before {
	// 		background-color: #44B875;
	// 	}
	// }
}

.btn+.btn {
	margin-left: 15px;

	@media (max-width: 991px) {
		margin: 0;
	}
}