.events {
	margin: 36rem 0 50rem;

	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 20rem;

		@media (max-width: 991px){
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__item {
		background: #F6F8FB;
		border-radius: 20px;
		padding: 27rem 34rem;
	}

	&__date {
		font-size: 13rem;
		line-height: 125.21%;
		color: #B8C8E0;
	}

	&__title {
		display: block;
		margin-top: 11rem;
		font-size: 20rem;
		line-height: 110.21%;
		color: $color-primary;
		transition: all 0.3s ease;

		&:hover {
			color: #383838;
		}
	}

	&__text {
		line-height: 110.21%;
		color: #353535;
		margin-top: 13rem;
	}

	&__desc {
		margin-top: 24rem;
		font-size: 13rem;
		color: #B8C8E0;
	}

	&__box {
		margin-top: 6rem;
		@include jcsb;
	}

	&__link {
		font-weight: 700;
		font-size: 13rem;
		line-height: 125.21%;
		text-decoration-line: underline;
		color: $color-primary;

		&:hover {
			text-decoration: none;
		}
	}

	&__btn {
		font-size: 10rem;
	}
}