.filter {
	position: relative;

	@media (min-width: 992px){
		margin-right: 40rem;
	}

	&:hover {
		.filter__menu {
			opacity: 1;
			visibility: visible;
			pointer-events: all;
		}
	}

	&__caption {
		position: relative;
		display: flex;
		align-items: center;
		cursor: pointer;
		z-index: 2;
	}

	&__desc {
		font-weight: 700;
		font-size: 10rem;
		text-transform: uppercase;
		color: #6D97EB;
		margin-right: 30rem;
	}

	&__menu {
		position: absolute;
		top: -22rem;
		left: -28rem;
		opacity: 0;
		visibility: hidden;
		background: #FFFFFF;
		border: 2px solid #DBE3EF;
		box-shadow: 0px 4px 74px #DBE3EF;
		border-radius: 30px;
		padding: 55rem 33rem 20rem 20rem;
		min-width: 237rem;
		transition: all 0.3s ease;
		pointer-events: none;
		z-index: 1;

		@media (max-width: 991px){
			left: -10rem;
		}
	}

	&__link {
		display: inline-block;
		font-weight: 700;
		font-size: 10rem;
		text-transform: uppercase;
		color: #B9C7DD;
		padding: 15rem 10rem;

		&:hover {
			background: rgba(109, 151, 235, 0.1);
			color: #6d97eb;
			border-radius: 50px;
		}
	}

	&--green {
		.filter__desc {
			color: #22591C;
		}

		.filter__menu {
			min-width: 148rem;

			@media (max-width: 991px){
				min-width: 134rem;
			}
		}

		.filter__link {
			&:hover {
				background: rgba(34, 89, 28, 0.1);
				color: #22591C;
				border-radius: 50px;
			}
		}
	}
}