.box {
	margin: 36rem 0 50rem;

	&__item {
		@include jcsb;
		background: #F6F8FB;
		border-radius: 15px;
		border: 1px solid #ECF0F6;
		padding: 25rem 35rem;

		&--green {
			.box__text:hover {
				color: #22591C;
			}

			.box__btn {
				color: #22591C;
			}

			.box__btn:before {
				background-color: #22591C;
			}

			.box__new {
				background-color: #22591C;
			}
		}

		&--violet {
			.box__text:hover {
				color: #856FFE;
			}

			.box__btn {
				color: #856FFE;
			}

			.box__btn:before {
				background-color: #856FFE;
			}

			.box__new {
				background-color: #856FFE;
			}
		}
	}

	&__item+&__item {
		margin-top: 20rem;
	}
&__left{
		padding-right: 15rem;
}
	&__desc {
		display: inline-block;
		padding-right: 70rem;
		position: relative;
		font-size: 13rem;
		color: #919191;
	}

	&__date {
		font-size: 13rem;
		color: #919191;
	}

	&__new {
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		padding: 5rem 10rem;
		background-color: #6D97EB;
		font-weight: 700;
		font-size: 9rem;
		text-transform: uppercase;
		color: #fff;
		border-radius: 30px;
	}

	&__text {
		margin-top: 11rem;
		display: block;
		font-size: 20rem;
		color: #4D4E4E;

		&:hover {
			color: #6D97EB;
		}
	}

	&__btn {
		background-color: #fff;
		color: #6D97EB;
		font-size: 10rem;
		border: none;

		&:hover {
			color: #fff;
		}

		&::before {
			background-color: #6D97EB;
		}
	}
}