.info {

	&__desc {
		font-size: 14rem;
	}

	&__subtitle {
		margin-top: 20rem;
		font-size: 20rem;
		font-weight: 700;
	}

	&__items {
		margin-top: 20rem;
	}

	&__item {
		display: flex;

		@media (max-width: 575px) {
			flex-direction: column;
			font-size: 14px;
		}
	}

	&__item+&__item {
		margin-top: 15rem;
	}

	&__label {
		margin-right: 10px;

		@media (min-width: 576px) {
			flex: 0 0 10%;
		}

		@media (max-width: 575px) {
			margin: 0 0 5px 0;
		}
	}

	&__mail {
		color: $color-primary;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}
}