 .footer {
 	padding: 75px 0 40px;
 	background: #F9F9F9;

 	@media (max-width: 767px) {
 		padding-top: 40px;
 	}

 	&__content {
 		display: grid;
 		grid-template-columns: 1fr 470px 1fr 1fr;
 		grid-gap: 60px;

 		@media (max-width: 1540px) {
 			grid-template-columns: repeat(2, 1fr);
 			grid-gap: 30px;
 		}

 		@media (max-width: 767px) {
 			grid-template-columns: 1fr;
 		}
 	}

 	&__copy {
 		font-size: 13px;
 		line-height: 130%;
 		color: rgba(56, 56, 56, 0.5);
 		margin-top: 80px;

 		@media (max-width: 767px) {
 			margin-top: 20px;
 		}
 	}

 	&__menu {
 		li+li {
 			margin-top: 15px;
 		}
 	}

 	&__link {
 		line-height: 15px;
 		color: #383838;

 		@media (max-width: 575px) {
 			font-size: 14px;
 		}
 	}

 	&__item {
 		display: flex;
 		flex-direction: column;
 		align-items: flex-end;

 		@media (max-width: 767px) {
 			align-items: flex-start;
 		}
 	}

 	&__dev {
 		margin-top: auto;
 		display: flex;
 		align-items: center;
 		justify-content: flex-end;

 		@media (max-width: 767px) {
 			margin-top: 15rem;
 		}

 		p {
 			font-size: 12rem;
 			line-height: 96.2%;
 			color: #1A1A1A;
 			margin-right: 10rem;
 		}
 	}

 	&-r {
 		margin-top: auto;
 		width: 100%;

 		&__top {
 			border-top: 1px solid #DBE3EF;
 		}

 		&__text {
 			max-width: 865rem;
 			padding: 38rem 0 47rem;
 			font-size: 17rem;
 			line-height: 125.21%;
 			color: #B9C7DD;

			 @media (max-width: 991px){
				 padding: 20rem 0;
			 }
 		}

 		&__bottom {
 			display: flex;
 			height: 100rem;
 			border-top: 1px solid #DBE3EF;
 			background: #F6F8FB;

			@media (max-width: 991px){
				padding: 15rem 0;
			}
 		}

 		&__content {
 			@include jcsb;
 			width: 100%;
 			max-width: 1310rem;
 			margin: auto;

 			@media (max-width: 991px) {
 				flex-direction: column;
 				padding: 0 15rem;
 			}
 		}

 		&__desc {
 			display: flex;
 			align-items: center;

 			img {
 				width: 47rem;
 				height: 42rem;
 			}

 			p {
 				max-width: 235rem;
 				font-size: 12rem;
 				line-height: 13rem;
 				color: #DBE3EF;
 				margin-left: 16rem;
 			}
 		}

 		&__dev {
 			display: flex;
 			align-items: center;
 			justify-content: flex-end;

 			@media (max-width: 991px) {
 				margin-top: 5rem;
 			}

 			p {
 				font-size: 12rem;
 				line-height: 96.2%;
 				color: #DBE3EF;
 				margin-right: 10rem;
 			}
 		}
 	}
 }