.circle {
	position: relative;

	&::before {
		content: '';
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: -20rem;
		left: -20rem;
		width: 80rem;
		height: 80rem;
		z-index: -1;
	}

	&--1 {
		&::before {
			background-image: url('../img/svg/circle-1.svg');
		}
	}

	&--2 {
		&::before {
			background-image: url('../img/svg/circle-2.svg');
		}
	}

	&--3 {
		&::before {
			background-image: url('../img/svg/circle-3.svg');
		}
	}

	&--4 {
		&::before {
			background-image: url('../img/svg/circle-4.svg');
		}
	}

	&--5 {
		&::before {
			background-image: url('../img/svg/circle-5.svg');
		}
	}
}