.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;

		@media (max-width: 991px){
			padding: 30rem 0;
		}
	}

	&__content {
		background-color: #fff;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		max-width: 1313rem;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70px);
		border-radius: 8px;

		@media (max-width: 991px) {
			margin: 0 15px;
			max-width: 440rem;
		}
	}

	&__close {
		position: absolute;
		right: -45rem;
		top: -45rem;
		font-size: 20px;
		color: #000;
		text-decoration: none;
		z-index: 3;

		@media (max-width: 991px){
			right: -45rem;
			top: -25rem;
		}

		img {
			width: 40rem;
			height: 40rem;
		}
	}

	&__wrap {
		display: flex;

		@media (max-width: 991px){
			flex-direction: column;
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
	}

	&__top {
		padding: 35rem;

		@media (max-width: 991px){
			padding: 20rem;
		}
	}

	&__title {
		font-size: 24rem;
		line-height: 110.21%;
		color: #353535;

		@media (max-width: 991px){
			font-size: 20rem;
		}
	}

	&__desc {
		margin-top: 25rem;
		line-height: 110.21%;
		color: #353535;
	}

	&__text {
		margin-top: 20rem;

		p+p {
			margin-top: 5px;
		}
	}

	&__bottom {
		margin-top: auto;
	}
}

.info-popup {
	@include jcsb;
	padding: 0 35px;

	&__date {
		font-size: 11rem;
		color: #C0CDE0;
	}

	&__comments {
		font-size: 11rem;
		text-decoration-line: underline;
		color: $color-primary;

		&:hover {
			text-decoration: none;
		}
	}
}

.share-popup {
	border-top: 1px solid #DBE3EF;
	@include jcsb;
	padding: 25rem 35rem;
	margin-top: 15rem;

	&__desc {
		font-size: 11rem;
		color: #C0CDE0;
	}

	&__icons {}

	&__icon {
		img {
			width: 30rem;
			height: 30rem;
		}

		&:not(:last-child) {
			margin-right: 5rem;
		}
	}
}