.lang {
	position: absolute;
	top: 28rem;
	right: 18rem;

	@media (max-width: 991px){
		top: 8rem;
		right: inherit;
		left: 165rem;
		z-index: 2;
	}

	&__content {
		position: relative;
	}

	&__content--active {
		.lang__list {
			opacity: 1;
			visibility: visible;
			top: 25rem;

			@media (max-width: 991px){
				top: 30rem;
			}
		}
	}

	&__link {
		display: flex;
		align-items: center;
		padding: 5rem 10rem;
		background: #EEF7FB;
		border-radius: 10px;

		span {
			color: $color-primary;
			transition: all 0.3s ease;
			font-weight: 700;
			font-size: 12rem;
			text-transform: uppercase;
		}
	}

	&__list {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 30rem;
		left: 0;
		border-radius: 0 0 5px 5px;
		z-index: 6;
		transition: all 0.3s ease;
	}
}