.video {
	margin: 40rem 0 50rem;

	&__content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 20rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__item {
		position: relative;

		&:hover {
			.video__desc {
				color: $color-primary;
			}
		}
	}

	&__video {
		height: 175rem;
		border-radius: 8px 8px 0 0;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 8px 8px 0 0;
		}
	}

	&__player {
		position: relative;
		background-color: transparent;
		display: flex;
		flex: 0 0 64%;
		height: 460rem;
		background-color: #E2E2E2;
		border-radius: 8px 0 0 8px;

		@media (max-width: 991px) {
			border-radius: 8px 8px 0 0;
		}

		iframe,
		img {
			width: 100%;
			height: 100%;
			display: block;
			object-fit: cover;
			border: none;
			border-radius: 8px 0 0 8px;

			@media (max-width: 991px) {
				height: 285rem;
				border-radius: 8px 8px 0 0;
			}
		}
	}

	&__btn {
		width: 121rem;
		height: 84rem;
		border: none;
		background-color: transparent;
		background-image: url("../img/svg/youtube-btn.svg");
		background-size: cover;
		background-repeat: no-repeat;
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 10;
		cursor: pointer;
	}

	&__body {
		padding: 20rem;
		border-radius: 0 0 10px 10px;
		background: #F6F8FB;
		border: 2px solid #ECF0F6;
	}

	&__date {
		font-size: 11rem;
		color: #919191;
	}

	&__desc {
		margin-top: 10rem;
		font-size: 15rem;
		line-height: 110.21%;
		color: #353535;
		min-height: 32rem;
		transition: all 0.3s ease;
	}

	&__new {
		position: absolute;
		top: 15rem;
		right: 10rem;
		border-radius: 30px;
		background-color: $color-primary;
		padding: 4rem 10rem;
		font-weight: 700;
		font-size: 9rem;
		text-transform: uppercase;
		color: #fff;
	}
}