.caption {
	&__content {
		@include jcsb;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__box {
		display: flex;
		align-items: center;

		@media (max-width: 991px){
			margin-top: 30rem;
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__form {
		position: relative;

		@media (max-width: 991px) {
			margin-top: 20rem;
		}

		input {
			background: #fff;
			border: 2px solid #DBE3EF;
			border-radius: 150px;
			font-size: 10rem;
			text-transform: uppercase;
			color: #B9C7DD;
			outline: none;
			height: 62rem;
			width: 420rem;
			padding: 0 55rem 0 30rem;
		}

		button {
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			width: 75rem;
			height: 62rem;

			img {
				width: 24rem;
				height: 24rem;
			}
		}
	}
}

.archive {
	margin-right: 66rem;

	&__link {
		font-weight: 700;
		font-size: 10rem;
		text-align: center;
		text-transform: uppercase;
		color: #B9C7DD;

		&.active {
			color: $color-primary
		}
	}
}