.hero {
	position: relative;
	background: linear-gradient(90deg, rgba(1, 156, 219, 1) 53%, rgba(27, 105, 178, 1) 89%);
	height: 100vh;
	min-height: 800px;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;

	&::before {
		content: '';
		background-image: url('../img/doctor.png');
		background-repeat: no-repeat;
		background-position: right;
		background-size: contain;
		position: absolute;
		top: 0px;
		right: 0px;
		width: 100%;
		height: 100%;

		@media (max-width: 991px){
			background-size: cover;
		}
	}

	&:after{
		@media (max-width: 991px){
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
			background-color: rgba(0, 0, 0, 0.1);
		}
	}

	&__title {
		position: absolute;
		top: 50%;
		left: 150rem;
		transform: translateY(-50%);
		font-size: 64rem;
		line-height: 107.71%;
		color: #fff;
		max-width: 860rem;
		z-index: 1;

		@media (max-width: 991px){
			left: 0;
			padding: 0 15rem;
			font-size: 36rem;
		}
	}

	&__bottom {
		width: 100%;
		max-width: 1360rem;
		height: 100rem;
		margin: 0 auto;
		@include jcsb;
		z-index: 1;

		@media (max-width: 991px){
			height: auto;
			padding: 15rem;
		}
	}

	&__desc {
		display: flex;
		align-items: center;
		font-size: 12rem;
		color: rgba(255, 255, 255, 0.2);
		max-width: 295rem;

		p {
			margin-left: 15rem;
		}
	}

	&__dev {
		display: flex;
		align-items: center;
		font-size: 12rem;
		color: rgba(255, 255, 255, 0.2);

		p {
			margin-right: 15rem;
		}
	}

}