.team {
	margin: 65rem 0 110rem;

	&__caption {
		@include jcsb;

		@media (max-width: 575px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__title {
		font-weight: 700;
		font-size: 36rem;
		color: #383838;
	}

	&__link {
		font-weight: 700;
		font-size: 12rem;
		text-decoration-line: underline;
		color: #BAD2E2;

		@media (max-width: 575px) {
			margin-top: 15px;
		}
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 20rem;
		margin-top: 20rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__img {
		position: relative;
		text-align: center;
		width: 345rem;
		height: 480rem;

		@media (max-width: 991px) {
			margin: auto;
			width: 250rem;
			height: 315rem;
		}

		&::before {
			content: '';
			background-color: #EBF5FB;
			position: absolute;
			bottom: 0px;
			left: 0;
			width: 100%;
			height: 80%;
			z-index: -1;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__desc {
		margin-top: 20rem;
		font-size: 13rem;

		@media (max-width: 991px) {
			text-align: center;
		}
	}

	&__name {
		margin-top: 10rem;
		color: $color-primary;
		font-weight: 700;
		font-size: 24rem;
		line-height: 110.21%;

		@media (max-width: 991px) {
			text-align: center;
			font-size: 18rem;
		}
	}
}