.inner-page {
	margin: 20rem 0 50rem;

	&__content {
		position: relative;
		max-width: 875rem;
	}

	&__link {
		position: absolute;
		top: 10rem;
		left: -85rem;
		width: 40rem;
		height: 40rem;
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			width: 13rem;
			height: 22rem;
		}

		@media (max-width: 991px){
			display: none;
		}
	}

	&__title {
		font-size: 64rem;
		line-height: 107.71%;
		color: #353535;

		@media (max-width: 991px) {
			font-size: 44rem;
		}
	}

	&__img {
		margin-top: 36rem;
		height: 450rem;

		@media (max-width: 991px) {
			height: 280rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		margin-top: 36rem;
		line-height: 130%;
		color: #353535;

		strong {
			font-size: 24rem;

			@media (max-width: 991px) {
				font-size: 18rem;
			}
		}

		p+p {
			margin-top: 10rem;
		}
	}
}