.presentations {
	margin: 40rem 0 50rem;

	&__item {
		background: #F6F8FB;
		border-radius: 15px;
		display: flex;
		align-items: center;
		padding-right: 30rem;
		border: 1px solid #ECF0F6;

		@media (max-width: 991px){
			flex-direction: column;
			padding-bottom: 20rem;
			border-radius: 0;
			padding-right: 0;
		}
	}

	&__item+&__item {
		margin-top: 20px;
	}

	&__img {
		width: 100%;
		max-width: 180rem;
		height: 100rem;

		@media (max-width: 991px){
			max-width: 100%;
			height: 200rem;
		}

		img {
			border-radius: 15px 0 0 15px;
			width: 100%;
			height: 100%;
			object-fit: cover;

			@media (max-width: 991px){
				border-radius: 0;
			}
		}
	}

	&__box {
		margin-left: 40rem;

		@media (max-width: 991px){
			margin-left: 0;
			padding: 20rem 0;
			text-align: center;
		}
	}

	&__date {
		font-size: 13rem;
		color: #919191;
	}

	&__text {
		display: block;
		margin-top: 10rem;
		font-size: 20rem;
		color: #4D4E4E;

		&:hover {
			color: #D04727;
		}
	}

	&__btn {
		margin-left: auto;
		background-color: #fff;
		color: #D04727;
		border: none;
		padding-right: 60rem;

		@media (max-width: 991px){
			margin-right: auto;
		}

		&:hover {
			color: #fff;
		}

		&::before {
			background-color: #D04727;
		}

		&:hover:after {
			background-image: url('../img/svg/presen-2.svg');
		}

		&::after {
			content: '';
			background-image: url('../img/svg/presen-1.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 15rem;
			transform: translateY(-50%);
			width: 18px;
			height: 18px;
		}
	}
}