.library {
	margin: 65px 0 50rem;

	&__content {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 25rem;

		@media (max-width: 991px){
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__link {
		border-radius: 20px;
		padding: 20rem 30rem;

		&:hover .library__desc:before {
			right: 5rem;
		}

		&--1 {
			background-color: #EDF6FC;

			.library__desc {
				color: $color-primary;

				&::before {
					background-image: url('../img/svg/arrow-right-lib-1.svg');
				}
			}

			.library__icon {
				background-color: #DEEFF7;
			}
		}

		&--2 {
			background-color: rgba(208, 71, 39, 0.102);

			.library__desc {
				&::before {
					background-image: url('../img/svg/arrow-right-lib-2.svg');
				}
			}

			.library__icon {
				background-color: rgba(208, 71, 39, 0.102);
			}
		}

		&--3 {
			background-color: rgba(34, 89, 28, 0.1);

			.library__desc {
				&::before {
					background-image: url('../img/svg/arrow-right-lib-3.svg');
				}
			}

			.library__icon {
				background-color: rgba(34, 89, 28, 0.1);
			}
		}

		&--4 {
			background-color: rgba(242, 171, 33, 0.1);

			.library__desc {
				&::before {
					background-image: url('../img/svg/arrow-right-lib-4.svg');
				}
			}

			.library__icon {
				background-color: rgba(242, 171, 33, 0.1);
			}
		}

		&--5 {
			background-color: rgba(109, 151, 235, 0.1);

			.library__desc {
				&::before {
					background-image: url('../img/svg/arrow-right-lib-5.svg');
				}
			}

			.library__icon {
				background-color: rgba(109, 151, 235, 0.1);
			}
		}

		&--6 {
			background-color: rgba(133, 111, 254, 0.08);

			.library__desc {
				&::before {
					background-image: url('../img/svg/arrow-right-lib-6.svg');
				}
			}

			.library__icon {
				background-color: rgba(133, 111, 254, 0.08);
			}
		}
	}

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 79rem;
		height: 79rem;
		border-radius: 50%;
	}


	&__desc {
		position: relative;
		margin-top: 35rem;
		font-size: 24rem;
		color: #383838;

		@media (max-width: 991px){
			font-size: 18rem;
			padding-right: 15rem;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: 13rem;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			width: 12rem;
			height: 20rem;
			transition: all 0.3s ease;
		}
	}
}