.block {
	display: flex;

	&--active {
		.aside {
			left: 0;
		}

		.mobile-header__icon:before,
		.mobile-header__icon:after {
			background-color: $color-primary;
		}

		.mobile-header__icon span {
			transform: scale(0) translate(0px, -50%);
		}

		.mobile-header__icon::before {
			top: 50%;
			transform: rotate(-45deg) translate(0px, -50%);
		}

		.mobile-header__icon::after {
			bottom: 50%;
			transform: rotate(45deg) translate(0px, 50%);
		}
	}
}

.aside {
	position: fixed;
	display: flex;
	flex-direction: column;
	width: 311rem;
	height: 100vh;
	background-color: #F6F8FB;
	border-right: 1px solid #DBE3EF;
	z-index: 3;

	@media (max-width: 991px) {
		width: 211rem;
		left: -100%;
		transition: 0.6s ease;
		// border-right: none;
	}
}

.logo-r {
	display: block;
	text-align: center;
	padding: 50rem 45rem 0 45rem;

	@media (max-width: 991px) {
		padding: 0;
	}

	img {
		width: 146rem;
		height: 72rem;
	}
}

.nav {
	padding: 0 45rem;
	margin: 150rem 0 50rem;

	@media (max-width: 991px) {
		padding: 0 15rem;
		margin-bottom: 30rem;
	}

	&__menu {
		li+li {
			margin-top: 28rem;

			@media (max-width: 991px) {
				margin-top: 18rem;
			}
		}
	}

	&__link {
		font-size: 12rem;
		text-transform: uppercase;
		padding: 0 20rem;

		@media (max-width: 991px) {
			padding: 0 10rem;
		}

		&.active {
			background-color: #eef7fb;
			border-radius: 100px;
			padding: 10rem 20rem;
			font-weight: 700;
			color: $color-primary;
		}
	}

	&__sub-list {
		position: relative;
		margin-top: 28rem;
		padding-left: 30rem;

		@media (max-width: 991px) {
			padding-left: 20rem;
		}

		&::before {
			content: '';
			background-image: url('../img/line.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: 100% 100%;
			position: absolute;
			top: -8px;
			left: 20rem;
			width: 11rem;
			height: 100%;

			@media (max-width: 991px) {
				left: 10rem;
			}
		}
	}

	&__sub-link {
		font-size: 12rem;
		text-transform: uppercase;
		padding: 0 20rem;

		@media (max-width: 991px) {
			padding: 0 10rem;
		}

		&.active {
			font-weight: 700;

			&--video {
				color: $color-primary;
			}

			&--broadcasts {
				color: #F2AB21;
			}

			&--discussions {
				color: #6D97EB;
			}

			&--presentations {
				color: #D04727;
			}

			&--master-classes {
				color: #22591C;
			}

			&--tips-tricks {
				color: #856FFE;
			}

		}
	}
}

.account {
	margin-top: auto;
	padding: 0 45rem;

	@media (max-width: 991px) {
		padding: 0 15rem;
	}

	&__content {
		background-color: #fff;
		box-shadow: 0px 12px 26px rgba(16, 30, 115, 0.06);
		border-radius: 8px;
		width: 222rem;
		padding: 5rem 20rem 5rem 5rem;
		@include jcsb;

		@media (max-width: 991px) {
			width: 175rem;
		}
	}


	&__img {
		width: 100%;
		height: 40rem;
		max-width: 40rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__name {
		font-weight: 700;
		font-size: 10rem;
		text-transform: uppercase;
		color: #3A3A3A;
		padding: 0 5rem;
	}
}

.socials {
	display: flex;
	align-items: center;
	justify-content: center;
	border-top: 1px solid #dbe3ef;
	margin-top: 20rem;
	height: 100rem;

	&__link {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
		border-radius: 50%;
		width: 30rem;
		height: 30rem;

		&:not(:last-child) {
			margin-right: 15rem;

			@media (max-width: 991px) {
				margin-right: 5rem;
			}
		}

		&:nth-child(1) {
			img {
				width: 18rem;
				height: 11rem;
			}
		}

		&:nth-child(2) {
			img {
				width: 10rem;
				height: 19rem;
			}
		}

		&:nth-child(3) {
			img {
				width: 16rem;
				height: 13rem;
			}
		}

		&:nth-child(4) {
			img {
				width: 16rem;
				height: 14rem;
			}
		}

		&:nth-child(5) {
			img {
				width: 12rem;
				height: 19rem;
			}
		}
	}
}