.mobile-header {
	position: fixed;
	top: 0;
	width: 100%;
	background-color: #F6F8FB;
	justify-content: space-between;
	align-items: center;
	padding: 5rem 15rem;
	display: none;
	z-index: 3;
	border-bottom: 1px solid #DBE3EF;

	@media (max-width: 991px) {
		display: flex;
	}

	&__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35rem;
		height: 20rem;
		cursor: pointer;
	}

	&__icon span,
	&__icon::before,
	&__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: $color-primary;
		border-radius: 10px;
	}

	&__icon::before,
	&__icon::after {
		content: "";
	}

	&__icon::before {
		top: 0;
	}

	&__icon::after {
		bottom: 0;
	}

	&__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}
}

.main-h .mobile-header {
	background-color: transparent;
	border-bottom: none;
}