.specialist {
	padding: 65rem 0 87rem;
	background-color: #F5FAFD;

	&__form {
		position: relative;
		margin-top: 72rem;

		@media (max-width: 575px) {
			margin-top: 30rem;
		}
	}

	&__input {
		background: #fff;
		border: 1px solid rgba(11, 138, 205, 0.13);
		border-radius: 150px;
		height: 75rem;
		width: 100%;
		padding: 0 85px 0 35px;
		color: rgba(56, 56, 56, 0.26);
		outline: none;
	}

	&__btn {
		position: absolute;
		top: 50%;
		right: 25px;
		transform: translateY(-50%);
		width: 75px;
		height: 75px;

		@media (max-width: 575px) {
			height: 50px;
			right: 0;
		}
	}

	&__content {
		margin-top: 87rem;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(400rem, 1fr));
		grid-gap: 25rem;

		@media (max-width: 575px) {
			margin-top: 40rem;
		}
	}

	&__item {
		background-color: #fff;
		padding: 27px 34px;
		transition: all 0.3s ease;
		border: 1px solid transparent;

		&:hover {
			border-color: #009EDD;
		}

		@media (max-width: 575px) {
			padding: 20px 15px;
		}
	}

	&__date {
		font-size: 12rem;
		color: rgba(33, 95, 170, 0.35);
	}

	&__subtitle {
		display: inline-block;
		margin-top: 15px;
		font-weight: 700;
		font-size: 20rem;
		line-height: 110.21%;
		color: #3A3A3A;
	}

	&__text {
		margin-top: 20rem;
		line-height: 110.21%;
		color: rgba(0, 0, 0, 0.64);
	}
}