.contacts {
	position: relative;

	&__content {
		@media (max-width: 991px) {
			background-color: #F5FAFD;
			padding: 60rem 0;
		}
	}

	&__box {
		max-width: 650rem;
		background-color: #fff;
		padding: 60rem;
		border-radius: 8px;

		@media (min-width: 992px) {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			z-index: 1;
		}

		@media (max-width: 991px) {
			max-width: 100%;
			padding: 40rem 20rem;
		}
	}

	&__title {
		margin-bottom: 45rem;
		font-weight: 700;
		font-size: 36rem;

		@media (max-width: 991px) {
			font-size: 24rem;
			margin-bottom: 20rem;
		}
	}

	&__btn {
		margin-top: 55rem;
	}
}

.map {
	&__canvas {
		width: 100%;
		height: 100%;
		margin: 0 auto;
	}

	&--main {
		height: 615rem;
	}

	&--inner {
		height: 863rem;

		.contacts__box {
			transform: translateY(-134%);
		}
	}
}