html {
	box-sizing: border-box;
	font-size: 0.052083333333333336vw;

	@media (max-width: 991px) {
		font-size: 0.18vw;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320px;
	font-family: "GothamPro", sans-serif;
	color: rgba(0, 0, 0, 0.75);
	font-size: 16rem;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: rgba(0, 0, 0, 0.75);
	transition: all 0.3s ease;

	&:hover {
		color: $color-primary;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
	padding-top: 96px;
}

.main-h {
	width: 100%;
	margin-left: 311rem;

	@media (max-width: 991px) {
		margin-left: 0;
	}
}

.main-i {
	width: 100%;
	height: 100vh;
	display: flex;
	flex-direction: column;
	padding-top: 56rem;
	margin-left: 311rem;
	overflow-y: auto;

	@media (max-width: 991px) {
		margin-left: 0;
		padding-top: 120rem;
	}
}

.container {
	margin: 0 auto;
	padding: 0 15rem;
	max-width: 1540rem;

	@media (max-width: 767px) {
		max-width: none;
	}
}

.container-r {
	margin: 0 auto;
	padding: 0 15rem;
	max-width: 1310rem;

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-weight: 700;
	font-size: 64rem;
	color: #383838;

	@media (max-width: 991px) {
		font-size: 38rem;
	}

	@media (max-width: 575px) {
		font-size: 22rem;
	}
}