.breadcrumbs {
	position: relative;
	z-index: 1;
	padding-bottom: 25rem;

	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__item {
		font-size: 14rem;

		@media (max-width: 991px) {
			padding-bottom: 10rem;
		}
	}

	&__item+&__item {
		padding-left: 10rem;

		&::before {
			content: "/";
			display: inline-block;
			padding-right: 10rem;
			color: rgba(56, 56, 56, 0.251);
		}
	}

	&__link {
		color: rgba(56, 56, 56, 0.251);
	}
}