.intro {
	margin-top: -96px;

	&>.container {
		position: relative;
	}

	&__desc {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		max-width: 1090px;
		font-weight: 700;
		font-size: 64rem;
		line-height: 107.71%;
		color: #fff;

		@media (max-width: 991px) {
			left: 0;
			padding: 0 15px;
		}

		@media (max-width: 991px) {
			font-size: 32rem;
		}
	}

	&__img {
		width: 100%;
		height: 940rem;

		@media (max-width: 991px) {
			height: 100vh;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__container {
		position: relative;
		margin: 0 auto;
		padding: 0 15px;
		max-width: 1540px;
	}

	.swiper-pagination {
		text-align: left;
		bottom: 290rem;

		@media (max-width: 991px) {
			bottom: 20rem;
			left: 15px;
		}
	}

	.swiper-pagination .swiper-pagination-bullet {
		width: 16px;
		height: 16px;
		background: linear-gradient(90deg, #009EDD 0%, #215FAA 100%);
		margin: 0 6px;
	}
}