.rsc {
	&__content {
		display: flex;
		justify-content: flex-end;
		max-width: 1920rem;
		margin: auto;

		@media (max-width: 991px) {
			justify-content: center;
			flex-direction: column;
			align-items: center;
			padding: 0 15rem;
		}
	}

	&__breadcrumbs {
		margin-top: 65rem;
	}

	&__left {
		flex: 0 0 48.5%;
		padding: 0 78rem 30rem 15rem;

		@media (max-width: 991px) {
			padding: 0 0 30rem 0;
		}
	}

	&__title {
		max-width: 620rem;
	}

	&__text {
		margin-top: 40rem;
		font-size: 20rem;
		line-height: 134.21%;

		@media (max-width: 575px) {
			margin-top: 20rem;
			font-size: 16rem;
		}

		p+p {
			margin-top: 20rem;
		}
	}

	&__list {
		margin-top: 20rem;
		list-style: disc inside;
	}

	&__item+&__item {
		margin-top: 15rem;
	}
}