.advantage {
	background: linear-gradient(92.65deg, rgba(203, 226, 247, 0.1) 1.47%, rgba(0, 133, 204, 0.079) 99.33%);
	padding-bottom: 200rem;

	@media (max-width: 991px) {
		padding: 40rem 0 100rem;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(300rem, 1fr));
		grid-gap: 20px;
		margin-top: -135px;
		position: relative;
		z-index: 1;

		@media (max-width: 991px) {
			margin-top: 0;
		}
	}

	&__item {
		padding: 45px;
		border: 1px solid #D9E6ED;
		background-color: #fff;

		@media (max-width: 1545px) {
			padding: 30px 20px;
		}
	}

	&__desc {
		margin-top: 35px;
		font-size: 21rem;
		line-height: 120%;
		color: $color-primary;

		@media (max-width: 991px) {
			font-size: 16rem;
			margin-top: 20px;
		}
	}

	&__content {
		display: flex;
		margin-top: 135rem;

		@media (max-width: 991px) {
			flex-direction: column;
			margin-top: 80rem;
		}
	}

	&__left {
		flex: 1 1 55%;
		margin-right: 53rem;

		@media (max-width: 991px) {
			margin: 0 0 30rem 0;
		}
	}

	&__title {
		font-weight: bold;
		font-size: 36rem;
		line-height: 115%;
		color: $color-primary;

		@media (max-width: 991px) {
			font-size: 23rem;
		}
	}

	&__text {
		margin-top: 40px;

		@media (max-width: 575px) {
			margin-top: 20px;
		}

		p {
			font-size: 20rem;
			line-height: 130%;

			@media (max-width: 991px) {
				font-size: 16rem;
			}
		}

		p+p {
			margin-top: 20px;
		}
	}

	&__link {
		color: $color-primary;
		font-weight: 700;
		font-size: 20rem;
		line-height: 99.21%;
		text-decoration: underline;

		@media (max-width: 991px) {
			font-size: 16rem;
		}

		&:hover {
			text-decoration: none;
		}
	}

	&__btn {
		margin-top: 40rem;
	}

	&__img {
		width: 573rem;
		height: 430rem;

		@media (max-width: 991px) {
			width: 100%;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}